import React, { useState } from 'react';
import { useTypedDispatch } from '../../hooks/useTypedDispatch';
import { useTypedSelector } from '../../hooks/useTypedSelector';
import { addItem } from '../../redux/slices/cartSlice';
import { ICartItem } from '../../types';

export const typeName = ['', ''];

type PropType = {
	id: number,
	name: string,
	price: number,
	imageUrl: string,
	sizes: number[],
	types: number[]
}

const PizzaBlock: React.FC<PropType> = ({ id, name, price, imageUrl, sizes, types }) => {
	const dispatch = useTypedDispatch();
	const [activeSize, setActiveSize] = useState(sizes[0]);
	const [activeType, setActiveType] = useState(0);
	const { items } = useTypedSelector((state) => state.cart);
	let addedCount = 0;
	items.map((el: ICartItem) => {
		if (el.id === id) {
			addedCount += el.count;
		}
		return 0;
	});
	const onAddToCart = () => {
		const pizzaObj = { id, name, price, imageUrl, size: activeSize, type: types[activeType], count: 0 };
		dispatch(addItem(pizzaObj));
	};

	return (
		<div className="pizza-block-wrapper">
			<div className="pizza-block">
				<img className="pizza-block__image" src={imageUrl} alt="Pizza" />
				<h4 className="pizza-block__title">{name}</h4>
				<div className="pizza-block__selector">
					<ul>
						{types.map((el, i) => (
							<li
								key={el + i}
								className={activeType === i ? 'active' : ''}
								onClick={() => setActiveType(i)}>
								{typeName[el]}
							</li>
						))}
					</ul>
					<ul>
						{sizes.map((el, i) => (
							<li
								key={el + i}
								className={activeSize === el ? 'active' : ''}
								onClick={() => setActiveSize(el)}>
								{el}
							</li>
						))}
					</ul>
				</div>
				<div className="pizza-block__bottom">
					<div className="pizza-block__price"> {price} </div>
					<button onClick={onAddToCart} className="button button--outline button--add">
						<svg
							width="12"
							height="12"
							viewBox="0 0 12 12"
							fill="none"
							xmlns="http://www.w3.org/2000/svg">
							<path
								d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
								fill="white"
							/>
						</svg>
						<span></span>
						{addedCount > 0 && <i>{addedCount}</i>}
					</button>
				</div>
			</div>
		</div>
	);
};

export default PizzaBlock;
